@font-face {
    font-family: Montserrat;
    src: url(./font/Montserrat.ttf);
}
@font-face {
    font-family: Montserrat-Bold;
    src: url(./font/Montserrat-Bold.ttf);
}


.footer {
    display: block;
    border-top: 2px solid #AEB6C1;
    margin-top: 145px;
    position: relative;
}

.footer__menu {
    margin-top: 60px;
}

.footer__menu h3{
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold !important;
    font-size: 12px;
    line-height: 163.42%;
    text-transform: uppercase !important;
    color: #AEB6C1 !important;    
}

.footer__menu a{
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder !important;
    font-size: 14px;
    line-height: 200% !important;
    text-decoration: none;
    color: #202020 !important;    
}

.footer__menu li{
    list-style: none !important;
}

.newsletter {
    width: 293px;
    height: 293px;
    background: #FFFFFF;
    border: 2px solid #AEB6C1;
    box-sizing: border-box;
    border-radius: 100%;
    padding: 50px;
    transform: translateY(-40%);
    position: absolute;
    top: 0;
    right: 0;
}

.newsletter h3 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold !important;;
    font-size: 16px;
    line-height: 163.42% !important;;
    align-items: center;
    text-align: center;
    text-transform: uppercase !important;;
    color: #202020 !important;    
}

.newsletter label {
    font-family: Montserrat;
    font-style: normal !important;
    font-weight: bolder !important;
    font-size: 12px;
    line-height: 18px !important;
    display: flex;
    align-items: center !important;
    color: rgba(32, 32, 32, 0.5) !important;    
}

.newsletter input {
    border: none;
    border-bottom: 2px solid #AEB6C1;
    margin-bottom: 27px;
}

.newsletter button {
    background: #DE8F75;
    border-radius: 3px;
    border: none;
    width: 110px;
    height: 50px;
    display: block;
    margin: 0 auto;
}

.send a{
    font-family: Open Sans;
    font-style: normal !important;
    font-weight: bolder !important;
    font-size: 14px;
    line-height: 23px;
    display: block !important;
    margin: 0 auto;
    align-items: center !important;
    text-align: center !important;
    text-transform: uppercase !important;
    color: #FFFFFF !important;    
}

.footer__main {
    display: grid;
    grid-template-columns: repeat(5, 20%);
}

.socialCertified img{
    margin-right: 30px;
    margin-bottom: 21px;
    margin-top: 30px;
}

.footer__copright {
    background: #DE8F75;
}

.footer__copright img{
    width: 110px;
    height: 25px;
    max-width: 110px;
}

.footer__copright span{
    display: inline-block;
    max-width: 730px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal !important;
    font-size: 12px !important;
    line-height: 18px !important;
    display: flex;
    align-items: center !important;
    color: #FFFFFF !important;
}

.footer__copright .conteiner {
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
}

