@font-face {
    font-family: Montserrat;
    src: url(./font/Montserrat.ttf);
}
@font-face {
    font-family: Montserrat-Bold;
    src: url(./font/Montserrat-Bold.ttf);
}

.productsuges{
    position: relative;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
}

.colorssuge {
    position: relative;
    display: flex;
    margin-left: auto;
    margin-top: 5px;
}

.colorssugeless {
    position: relative;
    display: flex;
    margin-left: auto;
    margin-top: 5px;
}

.titlecard {
    display: block;
    width: 100%;
    text-align: center;
    margin-top: 70px;
    margin-bottom: 20px;
}

.titlecard a {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder;
    font-size: 26px;
    line-height: 38px;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #DE8F75;
}

.product {
    margin-left: 13px;
}

.cardsuge {
    display: block;
    margin-right: 15px;
}

.cardsuge img {
    width: 100%;
    height: auto;
}

.infsuge {
    display: flex;
}

.pricesuge {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder;
    font-size: 16px;
    line-height: 23px;
    align-items: center;
    color: #202020;
}

.jazzberry {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #A9095E;
    margin-left: 25px;
}

.casal {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #2A5A75;
    margin-left: 15px;
}

.carmine {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #A14830;
    margin-left: 15px;
}

.black {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #000000;
    margin-left: 15px;
}

.backbtn {
    width: 35px;
    height: 35px;
    border: 2px solid #2A5A75;
    box-sizing: border-box;
    border-radius: 158%;
    margin-right: 5px;
}

.backbtn img{
    width: 9px;
    height: 14px;
    margin: 9px;
    display: block;
    position: relative;
}

.navgation {
    display: flex;
    position: absolute;
    left: 50%;
    bottom: -50px;
    transform: translateX(-50%);
}

.nextbtn {
    width: 35px;
    height: 35px;
    left: 90px;
    border: 2px solid #2A5A75;
    box-sizing: border-box;
    border-radius: 100%;
    margin-left: 5px;
}

.nextbtn img{
    width: 9px;
    height: 14px;
    display: block;
    position: relative;
    margin-left: 11px;
    margin-top: 8px;

}

.infpage {
    display: block;
    margin-top: 7px;
}


.infpage a {
    font-family: Open Sans;
    font-style: normal;
    font-weight: bolder;
    font-size: 14px;
    line-height: 23px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #2A5A75;
}




