.player {
    display: block;
    position: absolute;
    left: 6px;
    top: 0;
}


.videotxt {
    font-family: Montserrat;
    position: relative;
    display: block !important;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #DE8F75;
    width: 100%;
}

.playbtn {
    display: block;
    position: absolute;
    left: 23px;
    top: 23px;
}

.center .slick-center h3 {
    color: #e67e22;
    opacity: 1;
    -ms-transform: scale(1.08);
    transform: scale(1.08);
}


.slick-list img{
    width: 100%;
    height: auto;
}

.slick-dots {
    position: absolute !important;
    list-style: none !important;
    max-width: 82px;
    max-height: 383px;
    left: -130px;
    top: 110px;
}

.slick-dots li{
    display: block !important;
    height: auto !important;
    width: auto !important;

}

.slick-dots img{
    opacity: 0.5;
}

.slick-active img{
    opacity: 1;
}

.slick-prev {
    left: 40px !important;
    top: -16px !important;
    position: absolute !important;
    left: -94px !important;
    right: unset;
    top: 93px !important;
    bottom: unset;
}

.slick-next {
    top: 450px !important;
    right: 22px !important;
    transform: rotate(180deg) !important;
    position: absolute;
    left: -99px;
    bottom: 94px;
    top: unset !important;
    right: unset !important;
}

.slick-arrow {
    background-image: url("./files/arrow.png") !important;
    background-repeat: no-repeat !important;
    display: block;
    border: none;
    background: none;
    text-indent: 200%;
    white-space: nowrap;
    overflow: hidden;
    width: 24px;
}